<template>
  
    <audio ref="audio" ></audio>
  </div>
</template>

<script type="text/ecmascript-6">
  import { mapGetters, mapMutations, mapActions } from 'vuex'

  export default {
    data() {
      return {
        songReady: false,
        currentTime: 0,
      }
    },
    methods:{
      ...mapMutations({
        setFullScreen: 'SET_FULL_SCREEN'
      }),
      ...mapActions([
        'savePlayHistory'
      ])
    },
    computed:{
      ...mapGetters([
        'currentIndex',
        'fullScreen',
        'playing',
        'currentSong'
      ])
    },
    watch: {
      currentSong(newSong, oldSong) {
        if (!newSong.id || !newSong.url || newSong.id === oldSong.id) {N
          return
        }
      console.log(this.currentSong)
        this.$refs.audio.src = newSong.url
        this.$refs.audio.play()
      },
      playing(newPlaying) {
        if (!this.songReady) {
          return
        }
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">

</style>



